<!-- 客户管理 -->
<template>


  <div>

    <top-title title="订单管理" isShowClose="false">
    </top-title>
    <div class="master-content">
      <div class="master-content-nav">
        <div class="l">
          <Button class="btnColor" type="primary" @click="createNewOrder">新建订单</Button>
          <Button class="btnCancel" type="error" :disabled="isDeletes" @click="deleteAll">删除</Button>
          <Button type="text" v-on:click="trigger">筛选&nbsp;&nbsp;<Icon type="arrow-down-b"></Icon>
          </Button>
        </div>
        <div class="r">
          <Input v-model="orderName" placeholder="请输入订单名称" style="width: 300px">
          <!--  <Icon type="search" slot="append" @click.native="getFilterData"></Icon> -->
          </Input>
          <Button class="btnColor" type="primary" icon="search" @click="getFilterData">搜索</Button>
        </div>
      </div>
      <div class="master-content-middle" v-show="isShow">
        <div class="label">
          订单状态：
        </div>
        <div class="label-value">
          <Select v-model="orderStatus" style="width:200px" @on-change="getFilterData">
            <Option value="-99">全部</Option>
            <Option value="1">启用</Option>
            <Option value="0">删除</Option>
          </Select>
        </div>

        <div class="label">
          广告状态：
        </div>
        <div class="label-value">
          <Select v-model="deliverStatus" style="width:200px" @on-change="getFilterData">
            <Option value="all">全部</Option>
            <Option value="not-delete">非删除</Option>
            <Option value="delete">删除</Option>
            <Option value="draft">草稿</Option>
            <Option value="stop">暂停</Option>
            <Option value="before-deliver">待投放</Option>
            <Option value="in-deliver">投放中</Option>
            <Option value="after-deliver">投放完成</Option>
          </Select>

        </div>

      </div>
      <div class="master-content-content" style="margin-top: 10px">
        <Table border ref="selection" :columns="orderColumns" :data="orderData"
               @on-selection-change="selectData"></Table>
        <Page :total="totalCount" :current="pageNo" show-sizer show-total
              @on-change="changePageNo" @on-page-size-change="changePageSize"></Page>

      </div>


    </div>

    <new-order ref="newOrder" v-on:filterParent="getFilterData"></new-order>
    <show-order ref="showOrder"></show-order>
    <update-order ref="updateOrder" v-on:filterParent="getFilterData"></update-order>


  </div>
</template>
<script>
  import PageSize from '../../../common/PageSize'
  import topTitle from '@/components/common/topTitle'

  import showOrder from '@/components/pages/deliveryManagement/orderManagement/child/showOrder'
  import updateOrder from '@/components/pages/deliveryManagement/orderManagement/child/updateOrder'
  import newOrder from '@/components/pages/deliveryManagement/orderManagement/child/newOrder'

  export default {
    data() {
      return {
        isShow: false,
        orderName: "",
        orderStatus: "1",
        deliverStatus: "not-delete",
        isDeletes: true,
        deleteOrderParams: [],
        totalCount: 0,
        pageNo: 1,
        pageSize: 10,
        modal1: false,

        orderColumns: [
          {
            type: 'selection',
            width: 60,
            align: 'center'

          },
          {
            title: '订单名称',
            key: 'orderName',
            render: (h, params) => {
              return h('a', {
                on: {
                  click: () => {

                    this.$router.push({
                      path: '/orderAdvert',
                      query: {
                        id: params.row.orderId,
                        flag: '1',
                        orderName: params.row.orderName
                      }
                    })


                  }
                }

              }, params.row.orderName);
            }


          },
          {
            title: 'ID',
            key: 'orderId'
          },
          {
            title: '广告状态',
            key: 'deliverStatusListStr',
            render: (h, params) => {
              let deliverStatusList = params.row.deliverStatusList;
              let n = [];
              console.log(deliverStatusList);
              if (deliverStatusList && deliverStatusList.length > 0) {
                for (let d of deliverStatusList) {
                  let deliverStatus = d["deliverStatus"];
                  let totalNumber = d["totalNumber"];
                  let k = this.renderDeliverText(deliverStatus);
                  console.log(k);

                  let m = h('span',

                    {
                      class: 'deliver-status-all',
                      on: {
                        click: () => {
                          //alert(222)
                          this.$router.push({
                            path: '/orderAdvert',
                            query: {
                              id: params.row.orderId,
                              flag: '2',
                              orderName: params.row.orderName,
                              deliverStatus: deliverStatus
                            }
                          })


                          //this.$router.push({path: '/advertisingManagement'});

                        }
                      }
                    },

                    [k,
                      h('span', {
                          class: ['deliver-status-' + deliverStatus],
                        },
                        totalNumber)

                    ]
                  );
                  n.push(m);
                }
              }

              return h('a', n);
            }

          },
          {
            title: '广告客户',
            key: 'customName'
          },
          {
            title: '操作',
            key: 'orderAction',
            width: 200,
            align: 'center',
            render: (h, params) => {
              var orderStatus = params.row.orderStatus;
              if (orderStatus == 0) {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px',
                      backgroundColor: '#0676c4',
                      borderColor: '#0676c4'
                    },
                    on: {
                      click: () => {
                        this.$refs.showOrder.beforeLoad();
                        this.$refs.showOrder.getOrderData(params.row.orderId);

                      }
                    }

                  }, "查看"),

                  h('Button', {
                    props: {
                      type: 'info',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px',
                      backgroundColor: '#1fa2fd',
                      borderColor: '#1fa2fd'
                    },
                    on: {
                      click: () => {
                        //this.remove(params.index)
                        // this.$router.push({path:"/updateOrder",query:{"orderId":params.row.orderId}});


                        this.$refs.updateOrder.beforeLoad();
                        this.$refs.updateOrder.getOrderData(params.row.orderId);

                      }
                    }
                  }, '修改'),

                ]);
              }
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#0676c4',
                    borderColor: '#0676c4'
                  },
                  on: {
                    click: () => {

                      this.$refs.showOrder.beforeLoad();
                      this.$refs.showOrder.getOrderData(params.row.orderId);

                    }
                  }
                }, '查看'),
                h('Button', {
                  props: {
                    type: 'info',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    backgroundColor: '#1fa2fd',
                    borderColor: '#1fa2fd'
                  },
                  on: {
                    click: () => {
                      //this.remove(params.index)
                      // this.$router.push({path:"/updateOrder",query:{"orderId":params.row.orderId}});


                      this.$refs.updateOrder.beforeLoad();
                      this.$refs.updateOrder.getOrderData(params.row.orderId);

                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {

                    backgroundColor: '#ea0b00',
                    borderColor: '#ea0b00'
                  },
                  on: {
                    click: () => {
                      //this.remove(params.index)

                      let v = [];
                      v.push(params.row);

                      this.deleteBox(v);
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        orderData: []

      }
    },
    created() {
      //初始化数据
      this.getOrderData();

    },
    methods: {
      selectData: function (selection) {

        let canDeletes = selection.filter((item) => {
          return item.orderStatus == 1;
        });

        this.isDeletes = !canDeletes.length > 0;
        this.deleteOrderParams = canDeletes;


      },

      deleteAll: function () {
        this.deleteBox(this.deleteOrderParams);

      },
      createNewOrder: function () {
        this.$refs.newOrder.beforeLoad();
      },
      trigger: function () {

        this.isShow = !this.isShow;
      },
      deleteBox(params) {
        let orderNames = params.map((item, index) => {
          return item.orderName;
        });
        let orderIds = params.map((item, index) => {
          return item.orderId;
        });
        this.$Modal.confirm({
          title: '删除订单',
          content: `你确定要删除` + orderNames.join(","),
          onOk: () => {
            this.deleteOrderData(orderIds.join(","));
          }
        })
      },

      deleteOrderData: function (orderIds) {
        let url = "/order/deleteOrder.json?orderId=" + orderIds;
        this.$Http.get(url, {}).then(res => {
          this.$Message.info('删除订单成功!');

          this.getOrderData();
        });
      },

      getOrderData: function () {
        this.isDeletes = true;
        this.deleteCustomParams = [];

        let nameLike = this.orderName;

        let status = this.orderStatus;
        let deliverStatus = this.deliverStatus;
        let url = "/order/getOrderList.json?nameLike=" + encodeURIComponent(nameLike)
          + "&status=" + status + "&deliverStatus=" + deliverStatus + "&pageNo=" + this.pageNo
          + "&pageSize=" + this.pageSize;
        this.$Http.get(url, {}).then(res => {

          let result = res.body.data.result;
          this.orderData = [];
          for (let value of result) {
            let j = {};
            j["orderName"] = value["orderName"];
            j["orderStatus"] = value["orderStatus"];
            j["customName"] = value["customName"];
            j["orderId"] = value["fxAdOrderId"];
            let deliverStatusList = value["deliverStatusList"];
            // let deliverStatusListStr="";
            // for(let d of deliverStatusList){
            // 	let deliverStatus=d["deliverStatus"];
            // 	let totalNumber=d["totalNumber"];

            // 	deliverStatusListStr+=this.renderDeliverHtml(deliverStatus,totalNumber);
            // }
            j["deliverStatusList"] = deliverStatusList;
            this.orderData.push(j);
          }
          this.totalCount = res.body.data.totalCount;

        });

      },
      changePageNo: function (pageNo) {//改变每页页码
        // this.pageSize = 10;
        this.pageNo = pageNo;
        this.getOrderData();
      },
      changePageSize: function (pageSize) { //改变每页条数时的回调
        this.pageNo = 1;
        this.pageSize = pageSize;
        this.getOrderData();
      },
      getFilterData: function () {
        // this.pageSize = 10;
        // this.pageNo = 1;
        this.getOrderData();
      },
      renderDeliverText: function (deliverStatus) {

        switch (deliverStatus) {
          case 0:
            return "删除";
          case 1:
            return "草稿";
          case 2:
            return "暂停";
          case 3:
            return "待投放";
          case 4:
            return "投放中";
          case 5:
            return "投放完成";
        }
      }


    },


    components: {topTitle, showOrder, updateOrder, newOrder}

  }
</script>

<style>
  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
  }

  .master-content {
    background: #fff;
    height: 100%;
    padding: 10px;
  }

  .master-content-nav {
    display: -webkit-flex; /* Safari */
    display: flex;
    justify-content: space-between;
    padding: 10px 0px;
  }

  .master-content-middle {
    display: -webkit-flex; /* Safari */
    display: flex;
    padding: 30px 10px;
    border: 1px solid #d7f0f2;
    align-items: center;
  }

  .master-content-middle .label {
    padding: 0px 20px;
  }

  .deliver-status-all {
    display: inline-block;
    padding: 0px 6px;
  }

  .deliver-status-all span {
    display: inline-block;
    height: 18px;
    width: 18px;
    /*padding: 2px 5px;*/
    color: #ffffff;
  }

  .deliver-status-all .deliver-status-0 {
    background: #ea0b00;
    text-align: center;

  }

  .deliver-status-all .deliver-status-1 {
    background: #8b8b8b;
    text-align: center;

  }

  .deliver-status-all .deliver-status-2 {
    background: #f5a500;
    text-align: center;

  }

  .deliver-status-all .deliver-status-3 {
    background: #1fa2fd;
    text-align: center;

  }

  .deliver-status-all .deliver-status-4 {
    background: #0676c4;
    text-align: center;

  }

  .deliver-status-all .deliver-status-5 {
    background: #19be6b;
    text-align: center;

  }

  .box-show {
    border: 1px solid red;

  }


</style>



