<template>
  <div>
    <Modal
      v-model="modal1"
      title="查看订单" :styles="{width:'100%'}" class-name="modal-box">

      <div class="master-content">
        <div class="row"><label>订单名称:</label>{{orderName}}</div>
        <div class="row"><label>广告客户:</label>
          {{customName}}
        </div>

        <div class="row"><label>合同号:</label>{{contractNo}}</div>


      </div>
      <div slot="footer">
        <Button type="text" @click="modal1=false">返回</Button>
      </div>
    </Modal>
  </div>
</template>
<script>

  export default {
    data() {
      return {
        orderName: "",
        customName: "",
        contractNo: "",
        orderId: "",
        modal1: false,
      }
    },
    // props:["orderId"],
    created() {
      // this.getOrderData();

    },
    methods: {
      beforeLoad: function () {//还原所有属性
        this.orderName = "";
        this.customName = "";
        this.contractNo = "";
        this.orderId = "";

      },


      getOrderData: function (orderId) {
        this.orderId = orderId;
        let url = "/order/getOrderById.json?orderId=" + this.orderId;
        this.$Http.get(url, {}).then(res => {
          let r = res.body.data;
          this.orderName = r["orderName"];
          this.contractNo = r["contractNo"];
          this.customName = r["customName"];
          this.modal1 = true;
        });
      }


    }

  }

</script>

<style scoped>
  .master-content {
    background: #fff;
    height: 100%;
    padding: 10px;
  }

  .master-content .row {
    padding: 10px;
    display: -webkit-flex; /* Safari */
    display: flex;
    align-items: center;

  }

  .master-content .row label {
    width: 100px;
  }

  .master-content .row.down {
    border-top: 1px solid #eaeaea;
    height: 100px;

  }


</style>



